// Row styles
// @include grid-row();
@mixin grid-row() {
	@include clearfix();
}

// Mixin to calculate width of column
// @include @grid-column(INT, VALUE, INT)
@mixin grid-column($thisElementColumns:4, $totalWidth: 100%, $totalColumns: $nbrOfColumns) {
	width: ($totalWidth / $totalColumns) * $thisElementColumns;
}